  }
}

.contacts__text-content {
  position: relative;
  margin-bottom: 74px;
  display: flex;
  justify-content: space-between;
}
.contacts__text-content::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -32px;
  width: 100%;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.2);
}
@media (max-width: 1199px) {
  .contacts__text-content::before {
    bottom: -27px;
  }
}
@media (max-width: 767px) {
  .contacts__text-content::before {
    bottom: -23px;
  }
}
.contacts__text-content h2 {
  margin-top: 6px;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 50px;
  line-height: 50px;
  letter-spacing: -1.6px;
}
@media (max-width: 1199px) {
  .contacts__text-content h2 {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 34px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .contacts__text-content h2 {
    font-size: 20px;
    line-height: 20px;
  }
}
.contacts__text-content dl {
  margin: 0;
  max-width: 604px;
  display: grid;
  grid-template-columns: 2.25fr 1.2fr 0.5fr;
  justify-content: space-between;
  gap: 25px;
}
@media (max-width: 1199px) {
  .contacts__text-content dl {
    max-width: 100%;
    width: 100%;
    gap: 65px;
  }
}
@media (max-width: 767px) {
  .contacts__text-content dl {
    grid-template-columns: 1fr;
    gap: 15px;
  }
}
.contacts__text-content dt {
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.12px;
  text-transform: uppercase;
  opacity: 0.8;
}
.contacts__text-content dd {
  margin-left: 0;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: -0.5px;
}
@media (max-width: 1199px) {
  .contacts__text-content dd {
    font-size: 16px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .contacts__text-content dd {
    font-size: 14px;
    line-height: 20px;
  }
}
@media (max-width: 1199px) {
  .contacts__text-content {
    margin-bottom: 58px;
    flex-direction: column;
    justify-content: stretch;
    gap: 29px;
  }
}
@media (max-width: 767px) {
  .contacts__text-content {
    margin-bottom: 54px;
  }
}

.contacts__details-link {
  font-size: 16px;
  line-height: 26px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .contacts__details-link {
    font-size: 16px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .contacts__details-link {
    font-size: 14px;
    line-height: 20px;
  }
}

.contacts__details-group {
  width: fit-content;
  display: flex;
  flex-direction: column;
  gap: 14px;
