  .footer__list {
    gap: 15px;
  }
}
@media (max-width: 767px) {
  .footer__list {
    margin-bottom: 15px;
    grid-column: 1/-1;
    grid-row: 2/3;
    gap: 7px 20px;
    justify-content: center;
  }
}

.footer__link {
  font-size: 16px;
  line-height: 26px;
}
@media (max-width: 1199px) {
  .footer__link {
    font-size: 16px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .footer__link {
    font-size: 14px;
    line-height: 20px;
  }
}

.footer__social-list {
  grid-column: 3/-1;
  justify-self: flex-end;
}
@media (max-width: 1199px) {
  .footer__social-list {
    grid-column: 2/-1;
    grid-row: 2/3;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    grid-column: 1/-1;
    grid-row: 4/5;
    justify-self: center;
  }
}

.footer__copyright {
  grid-column: 1/-1;
  display: flex;
  justify-content: space-between;
}
.footer__copyright span {
  font-size: 14px;
  line-height: 18px;
}
@media (max-width: 1199px) {
  .footer__copyright span {
    font-size: 12px;
    line-height: 12px;
    opacity: 0.6;
  }
}
@media (max-width: 1199px) {
  .footer__copyright {
    grid-row: 2/3;
    width: fit-content;
    flex-direction: column-reverse;
    gap: 10px;
  }
}
@media (max-width: 767px) {
  .footer__copyright {
    margin-bottom: 15px;
    grid-column: 1/-1;
    grid-row: 3/4;
    align-items: center;
    gap: 12px;
  }
}

.footer__copyright-link {
  position: relative;
  font-size: 14px;
  line-height: 18px;
}
.footer__copyright-link::before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #484848;
}
@media (max-width: 1199px) {
  .footer__copyright-link {
    width: fit-content;
